import React from 'react';
import { Link } from 'react-router-dom';

import me from "../../logo.png"


const Footer = () => (
    <div style={{ marginTop: '10%', marginLeft: '3%' }}>
        <section id="sidebar">
            <section id="intro"></section>
            <Link to="/" className="logo">
                <img src={me} alt="me" style={{ maxWidth: "10%" }} />
            </Link>
            <header>
                <h3 style={{ color: 'ghostwhite', fontWeight: 'bold' }}>Jordan Wootton</h3>
            </header>
            <section id="footer">
                <p className="copyright" style={{ color: 'ghostwhite' }}>&copy; Jordan Wootton <Link to="/">jgall1300@gmail.com</Link>.</p>
            </section>
        </section>
    </div>
);

export default Footer;
