import React from 'react';
import Socials from '../Socials'
import logo from '../../logo.png';


import ScrollToTop from '../NavBar/ScrollToTop';
import Navigation from '../NavBar/Navigation';



const Home = () => (
    <div className="App">
        <header className="App-header">
            <div style={{ maxWidth: '70%', maxHeight: '70%', paddingTop: '3%' }}>
                <ScrollToTop />
                <Navigation />

                <div>about</div>

                <Socials />
            </div>
        </header>
        <footer className="App-footer">
            <img src={logo} style={{ width: '3%', height: '3%' }} className="App-logo" alt="logo" />
        </footer>
    </div >
);

export default Home;
